@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  :root {
    --background: 0 0% 100%;
    --foreground: 222.2 84% 4.9%;
    --card: 0 0% 100%;
    --card-foreground: 222.2 84% 4.9%;
    --popover: 0 0% 100%;
    --popover-foreground: 222.2 84% 4.9%;
    --primary: 221.2 83.2% 53.3%;
    --primary-foreground: 210 40% 98%;
    --secondary: 210 40% 96%;
    --secondary-foreground: 222.2 84% 4.9%;
    --muted: 210 40% 96%;
    --muted-foreground: 215.4 16.3% 46.9%;
    --accent: 210 40% 96%;
    --accent-foreground: 222.2 84% 4.9%;
    --destructive: 0 84.2% 60.2%;
    --destructive-foreground: 210 40% 98%;
    --border: 214.3 31.8% 91.4%;
    --input: 214.3 31.8% 91.4%;
    --ring: 221.2 83.2% 53.3%;
    --radius: 0.5rem;
  }

  .dark {
    --background: 222.2 84% 4.9%;
    --foreground: 210 40% 98%;
    --card: 222.2 84% 4.9%;
    --card-foreground: 210 40% 98%;
    --popover: 222.2 84% 4.9%;
    --popover-foreground: 210 40% 98%;
    --primary: 217.2 91.2% 59.8%;
    --primary-foreground: 222.2 84% 4.9%;
    --secondary: 217.2 32.6% 17.5%;
    --secondary-foreground: 210 40% 98%;
    --muted: 217.2 32.6% 17.5%;
    --muted-foreground: 215 20.2% 65.1%;
    --accent: 217.2 32.6% 17.5%;
    --accent-foreground: 210 40% 98%;
    --destructive: 0 62.8% 30.6%;
    --destructive-foreground: 210 40% 98%;
    --border: 217.2 32.6% 17.5%;
    --input: 217.2 32.6% 17.5%;
    --ring: 224.3 76.3% 94.1%;
  }
}

@layer base {
  * {
    @apply border-border;
  }
  body {
    @apply bg-background text-foreground;
  }
}

/* Custom hospital theme styles */
.hospital-gradient {
  background: linear-gradient(135deg, #10b981 0%, #3b82f6 100%);
}

/* App background: soft gradient with subtle texture */
.app-bg {
  background: radial-gradient(1200px 800px at -10% -10%, rgba(59, 130, 246, 0.08), transparent 60%),
              radial-gradient(1000px 700px at 110% 10%, rgba(16, 185, 129, 0.08), transparent 60%),
              linear-gradient(180deg, #f8fafc 0%, #f1f5f9 100%);
}

/* Glass card headers */
.glass-header {
  @apply bg-white/60 backdrop-blur-md border border-white/40 shadow-sm;
}

.batch-a {
  @apply bg-batch-a text-white;
}

.batch-b {
  @apply bg-batch-b text-white;
}

.workload-low {
  @apply bg-workload-low text-white;
}

.workload-medium {
  @apply bg-workload-medium text-white;
}

.workload-high {
  @apply bg-workload-high text-white;
}

.coverage-good {
  @apply bg-coverage-good text-white;
}

.coverage-warning {
  @apply bg-coverage-warning text-white;
}

.coverage-critical {
  @apply bg-coverage-critical text-white;
}

.coverage-low {
  @apply bg-coverage-low text-white;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  @apply bg-gray-100;
}

::-webkit-scrollbar-thumb {
  @apply bg-gray-300 rounded-full;
}

::-webkit-scrollbar-thumb:hover {
  @apply bg-gray-400;
}

/* Animation for loading states */
@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

.animate-pulse {
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

/* Print styles for reports */
@media print {
  .no-print {
    display: none !important;
  }
  
  .print-break {
    page-break-before: always;
  }
  
  body {
    font-size: 12px;
  }
}
